@font-face {
    font-family: "Gilroy-Light";
    src: url(./Gilroy-Light.otf)
}

@font-face {
    font-family: "Gilroy-ExtraBold";
    src: url(./Gilroy-ExtraBold.otf)
}

html {
    font-size: 14px;
}

body {
    margin: 0;
    padding: 0;
    font-family: "Gilroy-Light", sans-serif;
}

.page-wrapper {
    display: flex;
    height: 100vh;


    .col-left {
        flex: 1;
        background-color: black;
        display: flex;
        flex-direction: column;

        .menu {
            flex: 3;
            padding: 2rem 4rem;
            overflow: hidden;

            h1 {
                color: white;
                margin: 0;
                text-transform: uppercase;
                font-size: 1.7rem;
            }

            ol {
                margin: 2rem 0rem;
                padding: 0rem;
                
                li {
                    a {
                        text-decoration: none;
                        color: rgba(255, 255, 255, .75);
                    }

                    font-size: 1.2rem;
                    margin: 3rem 0;
                    padding: 0.5rem 0;
                    position: relative;
                    counter-increment: menucounter;

                    &::before {
                        position: absolute;
                        color: rgba(255, 255, 255, .6);
                        content: "0" counter(menucounter);
                        top: -20px;
                    }

                    &::marker {
                        font-size: 0;
                    }

                    &.active {
                        color: white;
                        a {
                            color: white;
                        } 
                        &::before {
                            color: white;
                        }

                        &::after {
                            width: 200%;
                            position: absolute;
                            height: 1px;
                            content: "";
                            bottom: 0;
                            left: 0;
                            border-bottom: 1px solid white;

                        }
                    }
                }
            }
        }

        .footer {
            flex: 2;
            display: flex;
            flex-direction: column;

            .top {
                font-size: 1.5rem;
                flex: 1;
                background-color: #e2743e;
                color: black;
                display: flex;
                justify-content: space-around;
                align-items: center;

                img { cursor: pointer; }
            }

            .bottom {
                flex: 4;
                background-image: url(./img/next_img.jpg);
                background-size: cover;
                padding: 1rem;
                display: flex;
                justify-content: space-between;
                align-items: flex-start;

                img {cursor: pointer;}
            }
        }
    }

    .col-center {
        flex: 2;
        display: flex;
        flex-direction: column;

        .heading {
            flex: 3;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 2rem 6rem;
            h2 {
                font-family: "Gilroy-ExtraBold", sans-serif;
                text-transform: uppercase;
                font-size: 4rem;
                margin: 0;
                span {
                    color: white;
                    text-shadow: -1px -1px 0, -1px -1px 0 #e39074, 1px 1px 0 #e39074;
                }
            }

            h3 {
                span {
                    font-family: "Gilroy-ExtraBold", sans-serif;
                }
            }
        }

        .hero {
            flex: 3;
            background-image: url(./img/hero.png);
            background-size: cover;
            display: flex;
            flex-direction: column;
            justify-content: flex-end;
            padding: 6rem;

            .container {
                width: 50%;

                h2 {
                    color: white;
                    font-family: "Gilroy-ExtraBold", sans-serif;
                    font-size: 3rem;
                }

                .actions {
                    display: flex;
                    justify-content: space-between;
                    align-items: center;

                    img {
                        cursor: pointer;
                    }

                }
            }
        }

    }

    .col-right {
        flex: 2;
        display: flex;
        flex-direction: column;

        .hero2 {
            background-image: url(./img/hero2.jpg);
            background-size: cover;
            flex: 3;
            display: flex;
            flex-direction: column;
            justify-content: space-between;

            .top {
                display: flex;
                justify-content: flex-end;
                padding: 2rem;

                img { cursor: pointer; }
            }

            .bottom {
                display: flex;
                justify-content: space-between;
                align-items: center;
                padding: 2rem;

                h2 {
                    color: white;
                    font-family: "Gilroy-ExtraBold", sans-serif;
                    font-size: 2.5rem;
                }
            }
        }

        .footer {
            flex: 2;
            display: flex;

            .left {
                flex: 1;
                background-color: black;
                color: white;
                padding: 2rem;
                line-height: 1.7;
                display: flex;
                flex-direction: column;
                justify-content: space-between;

                .actions, .likes, .share {
                    display: flex;
                    align-items: center;
                }

                .likes {
                    margin-right: 2rem;
                }

                .likes, .share {
                    span {
                        margin-left: 0.5rem;
                    }
                }
            }

            .right {
                flex: 1;
                display: flex;
                flex-direction: column;
                justify-content: center;
                text-align: center;

                h2 {
                    font-size: 2.5rem;
                    font-family: "Gilroy-ExtraBold", sans-serif;

                    span {
                        color: white;
                        text-shadow: -1px -1px 0, -1px -1px 0 #e39074, 1px 1px 0 #e39074;
                        display: block;
                    }
                }

                p {
                    margin: 0;
                    font-size: 1.2rem;

                    &.author {
                        font-family: "Gilroy-ExtraBold", sans-serif;
                    }
                }
            }
        }
    }

}